<template>
  <div class="recieve_form">
    <input placeholder="amount" type="number" name="amount" v-model="amount" required :disabled="formDisabled"/><br>
    <input placeholder="memo" type="text" name="memo" v-model="memo" :disabled="formDisabled"/><br>
    <div class="recieve_buttons">
      <button class="ok_button" type="button" v-on:click="cancel()" style="margin-right: 4px">Cancel</button>
      <button class="ok_button" type="button" v-on:click="send()">Send</button>
    </div>
    <qrcode-vue :value="lninvoice"  level="H" />
    {{ lninvoice }}
  </div>
</template>

<script>
import xhr  from '../services/xhr.service'
import QrcodeVue from 'qrcode.vue'
export default {
  name: "ReceiveForm",
  components: {
    QrcodeVue,
  },
  data() {
    return {
      amount:"",
      memo:"",
      lninvoice:"OK",
      formDisabled:false
    }
  },
  methods: {
    send() {
      if(this.formDisabled) return;
      this.formDisabled = true
      xhr.getInvoice(this.$store.state.token,{amount:this.amount,memo:this.memo},(data)=>{
        this.lninvoice = data.payment_request
      })
    },
    cancel() {
      this.amount = "";
      this.memo = "";
      this.formDisabled = false;
      this.$router.push({ path: '/account' })
    }
  }
}
</script>

<style scoped>
.recieve_form {
  text-align: center;
}
.recieve_buttons {
  display: block;
}
.ok_button
{
  cursor: pointer;
  width: 100px;
  height: 37px;
  background-color: #e7e7e7;
  border-style: none;
  font-size: 20px;
}
</style>